.newsletter .widget .title {
  font-size: 16px; }

.newsletter .widget form input {
  font-size: 16px;
  background-color: #f1f1f1;
  color: #333; }

.newsletter .widget form button {
  font-size: 16px;
  background: #f00;
  color: #fff; }

.newsletter-form {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-content: stretch;
  flex-wrap: wrap;
  margin: 48px auto; }
  .newsletter-form * {
    box-sizing: border-box; }
  .newsletter-form-col.fields, .newsletter-form-col.interests {
    max-width: 400px;
    width: 100%; }
  .newsletter-form-col.break {
    width: 1px;
    background-color: #999; }
  .newsletter-form-col.button {
    margin-top: 32px;
    flex: 2 0 100%;
    text-align: center; }
  .newsletter-form.side {
    transition: right ease-in-out 500ms;
    background-color: #eee;
    border: 1px solid #333;
    border-right: 0;
    border-radius: 20px 0 0 20px;
    padding: 20px 40px;
    position: fixed;
    top: 10px;
    bottom: 10px;
    right: 0;
    min-width: 320px;
    max-width: 500px; }
  .newsletter-form button {
    display: inline-block;
    background-color: #f00;
    text-transform: uppercase;
    color: #fff;
    font-size: 16px;
    font-weight: 700;
    font-family: 'Open Sans', sans-serif;
    text-align: center;
    border-radius: 20px;
    border: none;
    max-width: 400px;
    width: 100%;
    padding: 8px; }
  .newsletter-form.hide {
    right: -100%; }
  .newsletter-form .input-field + .input-field {
    margin-top: 48px; }
  .newsletter-form .input-field label {
    display: block;
    font-size: 16px;
    font-weight: 600;
    padding-left: 19.2px;
    margin: 5px 0 0; }
  .newsletter-form .input-field input {
    display: block;
    border-radius: 20px;
    padding: 10px;
    border: 1px solid #333;
    width: 100%; }
  .newsletter-form .interests h4 {
    margin: 0;
    text-transform: uppercase;
    font-size: 19.2px; }
  .newsletter-form .interests .checkboxes {
    display: flex;
    flex-wrap: wrap; }
  .newsletter-form .interests .checkbox-field {
    flex: 0 0 50%;
    margin: 5px 0; }
    .newsletter-form .interests .checkbox-field label {
      cursor: pointer;
      font-size: 16px;
      text-transform: uppercase; }
